.city-list-dd-container {
    position: absolute;
    background-color: var(--color-lightest);
    border: 1px solid var(--color-dark);
    text-align: left;
    top: 0;
    width: calc(100% - 2.6rem);
    left: 1.3rem;
    margin-top: 3.1rem;
    z-index: 1;
}



