.weather-current-column {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.weather-current-column > div {
    margin: 0.5rem;
}

.weather-current-item {
    height: 2.5rem;
    font-size: 2rem;
    display: -ms-flexbox;
    display: flex;
