.weather-current-header {
    font-weight: bold;
    margin-right: 0.5rem;
}

.weather-current-data {
}

.weather-current-summary-container {
    -ms-flex-order: 1;
        order: 1;
}

.weather-current-icon-container {
    -ms-flex-order: 2;
