@media (max-width: 516px) {
    .weather-current-icon-container {
        -ms-flex-order: 3;
            order: 3;
    }

    .weather-current-coniditions-container {
        -ms-flex-order: 2;
            order: 2;
    }
}

/* entries */

.weather-current-main-item {
    padding: 0 10px 0 10px;
}

.weather-current-descrIcon {
    padding: 0;
    -ms-flex-pack: center;
        justify-content: center;
