/* buttons */
.weather-current-unit-switch {
    cursor: pointer;

    font-size: 3.5rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    border-radius: 0.5rem;
    background-color: var(--color-lightest-xparent);
    border: none;
    overflow: hidden;
    outline: none;
    color: var(--color-darkest);
    line-height: 1.2rem;
}

.weather-current-unit-switch:hover:enabled {
    background-color: var(--color-accent);
}

.weather-current-favourite-button {
    border: none;
    cursor: pointer;
    overflow: hidden;
    width: 3.2rem;
    height: 3.2rem;
    font-size: 3rem;
    border-radius: 50%;
    line-height: 0.7rem;
    text-align: center;
    outline: none;
}

.weather-current-favourite-button:hover:enabled {
    background-color: CornflowerBlue;
}

.weather-current-favourite-button:disabled {
    cursor: not-allowed;
}

.weather-current-favourite-no {
    color: var(--color-light);
    background-color: var(--color-lightest-xparent);
}

.weather-current-favourite-yes {
    color: var(--color-bright);
    background-color: var(--color-neutral);
}



